.review-btn {
  
  color: #fff;
  padding: 10px 30px;
  border-radius: 100px;
  background: -webkit-linear-gradient(120deg,#36ECFF, #41C2FF 70%, #09B0FF);
  background: -o-linear-gradient(120deg,#36ECFF, #41C2FF 70%, #09B0FF);
  background: linear-gradient(-30deg,#36ECFF, #41C2FF 70%, #09B0FF);
  position: absolute;
  top: 360px;
  right: 35%;
  cursor: pointer;
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8)
}

.review-btn:hover {
  background: #09B0FF;
  
  
}


@media (min-width: 768px) and (max-width: 1000px) {
  .review-btn {
    -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
            transform: scale(0.7);
    top:249px;
    right: 5px;
  }
}


